import lModal from '@/components/l-modal/l-modal.vue'
export default {
	components: {
		lModal,
	},
	data() {
		return {
			classifyList:[],
			currentTab:0,
			isShowModal: false, // 是否展示弹框
			type: '', // 弹框类型
			modalTitle: '',// 标题
			chooseItem: {}, // 所选内容
			operatName: ""
		};
	},
	mounted(){
		this.getAttentionOrganize()
	},
	methods:{
		getAttentionOrganize(){
			let memberId = JSON.parse(uni.getStorageSync('userInfo')).id
			this.$Request.get(`/appAttentionController.do?getAttentionOrganize&type=1&memberId=${memberId}`)
			.then(res => {
				if(res.code == 0){
					this.classifyList = res.data.organizeList
					this.currentTab = this.classifyList[0].id
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		clickTop(item){
			let memberId = JSON.parse(uni.getStorageSync('userInfo')).id
			this.currentTab = item.id
			let middleValue = item;
			this.classifyList.splice(this.classifyList.indexOf(item), 1);
			this.classifyList.unshift(middleValue)
			this.$Request.get(`/appAttentionController.do?topAttentionOrganize&type=1&organizeId=${item.id}&memberId=${memberId}`)
			.then(res => {
				if(res.code == 0){
					this.getAttentionOrganize()
				}
				uni.showToast({
					title: res.info,
					icon: 'none'
				})
			})
		},
		clickOperation(type, item){
			this.isShowModal = true
			this.type = type
			this.modalTitle = ''
			this.chooseItem = {}
			this.operatName = ''
			switch(this.type) {
				case 'add':
					this.modalTitle = '新增分组'
					break
				case 'edit':
					this.modalTitle = '编辑分组'
					break
				case 'delete':
					this.modalTitle = '提示'
					break
			}
			if(item) {
				this.chooseItem = item
				this.operatName = item.organizeName
			}
		},
		addAttentionOrganize(){
			let memberId = JSON.parse(uni.getStorageSync('userInfo')).id
			this.$Request.get(`/appAttentionController.do?addAttentionOrganize&type=1&memberId=${memberId}&name=${this.operatName}`)
			.then(res => {
				if(res.code == 0){
					this.getAttentionOrganize()
				}
				uni.showToast({
					title: res.info,
					icon: 'none'
				})
			})
		},
		updateAttentionOrganize(){
			let memberId = JSON.parse(uni.getStorageSync('userInfo')).id
			this.$Request.get(`/appAttentionController.do?updateAttentionOrganize&organizeId=${this.chooseItem.id}&memberId=${memberId}&name=${this.operatName}`)
			.then(res => {
				if(res.code == 0){
					this.getAttentionOrganize()
				}
				uni.showToast({
					title: res.info,
					icon: 'none'
				})
			})
		},
		delAttentionOrganize(){
			let memberId = JSON.parse(uni.getStorageSync('userInfo')).id
			this.$Request.get(`/appAttentionController.do?delAttentionOrganize&organizeId=${this.chooseItem.id}&memberId=${memberId}`)
			.then(res => {
				if(res.code == 0){
					this.getAttentionOrganize()
				}
				uni.showToast({
					title: res.info,
					icon: 'none'
				})
			})
		},
		cancel(){
			this.isShowModal = false
			this.modalTitle = ''
			this.chooseItem = {}
			this.operatName = ''
		},
		confirm() {
			this.isShowModal = false
			console.log(this.operatName)
			switch(this.type) {
				case 'add':
					this.addAttentionOrganize()
					break
				case 'edit':
					this.updateAttentionOrganize()
					break
				case 'delete':
					this.delAttentionOrganize()
					break
			}
		},
	}
}
